body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: url("https://www.transparenttextures.com/patterns/hearts.png"), pink; /* Heart pattern */
  font-family: "Arial", sans-serif;
  overflow: hidden;
  position: relative;
}

/* Floating heart animation */
.heart-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;
}

.heart-bg span {
  position: absolute;
  bottom: -10px;
  background: red;
  width: 20px;
  height: 20px;
  clip-path: polygon(50% 0%, 100% 35%, 80% 100%, 50% 80%, 20% 100%, 0% 35%);
  animation: float 5s linear infinite;
  opacity: 0.7;
}

@keyframes float {
  0% {
    transform: translateY(0) scale(0.8);
    opacity: 1;
  }
  100% {
    transform: translateY(-100vh) scale(1.2);
    opacity: 0;
  }
}

/* Valentine Proposal Card */
.card-container {
  text-align: center;
  position: relative;
  z-index: 10;
}

.card {
  background: white;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  animation: popIn 0.5s ease-out;
}

.card h2 {
  color: red;
  font-size: 24px;
}

.card p {
  font-size: 18px;
}

.card button {
  background: red;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
  border-radius: 5px;
  margin-top: 10px;
}

.card button:hover {
  background: darkred;
}

.message {
  margin-top: 10px;
  font-size: 18px;
  font-weight: bold;
}

/* Pop-in animation */
@keyframes popIn {
  from {
    transform: scale(0);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}
